/*welcome module styles*/
#welcome {
    position: relative;
    margin-top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background-image: url('../images/macbook-pro-131187negativespace.jpg');
    background-size: cover;
    background-attachment: fixed;
}
#intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
}
#intro h1 {
    font-size: calc(max(1.5rem, min(3.5vw, 3.5rem)));
    letter-spacing: 3px;
    margin: 1em auto;
}
#intro h3 {
    font-size: calc(max(0.65rem, min(1.5vw, 1.5rem)));
    margin: 0 auto;
    color: #bbc;
    font-weight: 200;
    letter-spacing: 1px;
    font-family: 'dosis', sans-serif;
}
#intro span {
    color: #dde;
    display: inline-block;
    text-align: center;
    width: 40px;
}
#intro p {
    width: 90%;
    color: #eef;
    font-size: calc(max(0.75rem, min(1.5vw, 1.5rem)));
    letter-spacing: 1px;
    line-height: 1.6;
}


/*artwork module styles */
.itemContainer {
    position: relative;
    margin: 2em auto;
    margin-bottom: 6em;
    padding: 3em;
    padding-bottom: 4em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.75em  min(7%, 50px); 
    width: 80%;
}
.art {  /* the img tag*/
    width: 100%;
    height: auto;
    border-radius: 3px;
    border: 1px solid #223;
    background-color: #223; /* otherwise there is a border-img gap*/
}
.artGridItem:hover {
    z-index: 5;
    margin: 0 auto;
}
.artGridItem:hover .art{
    box-shadow: 2px 2px 10px 5px rgba(0,0,25,0.5);
}
.artGridItem.L:hover{
    transform: scale(1.65) translate(34%);
}
.artGridItem.R:hover{
    transform: scale(1.65) translate(-34%);
}